
import { useEffect, useState } from 'react';
import { useAuth } from '../../../contexts/AuthContext';
import { supabase } from '../../../lib/supabase';
import { useNavigate } from 'react-router-dom';

interface Consultation {
  id: string;
  user_id: string;
  title: string;
  content: string;
  status: 'pending' | 'in_progress' | 'resolved';
  created_at: string;
  updated_at: string;
}

export default function UserConsultation() {
  const { profile } = useAuth();
  const navigate = useNavigate();
  const [consultations, setConsultations] = useState<Consultation[]>([]);
  const [loading, setLoading] = useState(true);
  const [showForm, setShowForm] = useState(false);
  const [editingId, setEditingId] = useState<string | null>(null);
  const [formData, setFormData] = useState({
    title: '',
    content: '',
  });

  useEffect(() => {
    if (profile?.role !== 'user') {
      navigate('/admin/dashboard');
      return;
    }
    fetchConsultations();
  }, [profile]);

  const fetchConsultations = async () => {
    if (!profile?.id) return;

    setLoading(true);
    const { data, error } = await supabase
      .from('consultations')
      .select('*')
      .eq('user_id', profile.id)
      .order('created_at', { ascending: false });

    if (error) {
      console.error('相談データ取得エラー:', error);
    } else if (data) {
      setConsultations(data);
    }
    setLoading(false);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.title.trim() || !formData.content.trim()) {
      alert('タイトルと内容を入力してください');
      return;
    }

    if (editingId) {
      // 編集
      const { error } = await supabase
        .from('consultations')
        .update({
          title: formData.title,
          content: formData.content,
          updated_at: new Date().toISOString(),
        })
        .eq('id', editingId);

      if (error) {
        console.error('更新エラー:', error);
        alert('更新に失敗しました');
        return;
      }
    } else {
      // 新規作成
      const { error } = await supabase
        .from('consultations')
        .insert({
          user_id: profile?.id,
          title: formData.title,
          content: formData.content,
          status: 'pending',
        });

      if (error) {
        console.error('送信エラー:', error);
        alert('送信に失敗しました');
        return;
      }
    }

    setShowForm(false);
    setEditingId(null);
    setFormData({ title: '', content: '' });
    fetchConsultations();
  };

  const handleEdit = (consultation: Consultation) => {
    setEditingId(consultation.id);
    setFormData({
      title: consultation.title,
      content: consultation.content,
    });
    setShowForm(true);
  };

  const handleDelete = async (id: string) => {
    if (!confirm('この相談を削除しますか？')) return;

    const { error } = await supabase
      .from('consultations')
      .delete()
      .eq('id', id);

    if (error) {
      console.error('削除エラー:', error);
      alert('削除に失敗しました');
      return;
    }

    fetchConsultations();
  };

  const getStatusBadge = (status: string) => {
    const statusConfig = {
      pending: { label: '未対応', color: 'bg-gray-100 text-gray-700' },
      in_progress: { label: '対応中', color: 'bg-blue-100 text-blue-700' },
      resolved: { label: '解決済み', color: 'bg-green-100 text-green-700' },
    };

    const config = statusConfig[status as keyof typeof statusConfig];
    return (
      <span className={`px-3 py-1 rounded-full text-xs font-medium ${config.color}`}>
        {config.label}
      </span>
    );
  };

  const openNewForm = () => {
    setEditingId(null);
    setFormData({ title: '', content: '' });
    setShowForm(true);
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={() => navigate('/user/dashboard')}
                className="w-10 h-10 flex items-center justify-center text-gray-600 hover:bg-gray-100 rounded-lg transition-colors cursor-pointer"
              >
                <i className="ri-arrow-left-line text-xl"></i>
              </button>
              <div>
                <h1 className="text-2xl font-bold text-gray-900">相談窓口</h1>
                <p className="text-sm text-gray-600 mt-1">お困りごとをご相談ください</p>
              </div>
            </div>
            <button
              onClick={openNewForm}
              className="px-4 py-2 bg-pink-500 text-white rounded-lg hover:bg-pink-600 transition-colors font-medium whitespace-nowrap cursor-pointer"
            >
              <i className="ri-add-line mr-2"></i>
              新規相談
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {loading ? (
          <div className="text-center py-12">
            <div className="inline-block w-8 h-8 border-4 border-pink-500 border-t-transparent rounded-full animate-spin"></div>
            <p className="mt-4 text-gray-600">読み込み中...</p>
          </div>
        ) : consultations.length === 0 ? (
          <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-12 text-center">
            <div className="w-16 h-16 bg-pink-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <i className="ri-question-answer-line text-3xl text-pink-500"></i>
            </div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">相談履歴がありません</h3>
            <p className="text-gray-600 mb-6">お困りごとがあればお気軽にご相談ください</p>
            <button
              onClick={openNewForm}
              className="px-6 py-3 bg-pink-500 text-white rounded-lg hover:bg-pink-600 transition-colors font-medium whitespace-nowrap cursor-pointer"
            >
              <i className="ri-add-line mr-2"></i>
              新規相談を送信
            </button>
          </div>
        ) : (
          <div className="space-y-4">
            {consultations.map((consultation) => (
              <div
                key={consultation.id}
                className="bg-white rounded-xl shadow-sm border border-gray-100 p-6 hover:shadow-md transition-shadow"
              >
                <div className="flex items-start justify-between mb-3">
                  <h3 className="text-lg font-bold text-gray-900 flex-1">{consultation.title}</h3>
                  <div className="flex items-center gap-2">
                    {getStatusBadge(consultation.status)}
                    <button
                      onClick={() => handleEdit(consultation)}
                      className="w-10 h-10 flex items-center justify-center text-blue-600 hover:bg-blue-50 rounded-lg transition-colors cursor-pointer"
                      title="編集"
                    >
                      <i className="ri-edit-line text-xl"></i>
                    </button>
                    <button
                      onClick={() => handleDelete(consultation.id)}
                      className="w-10 h-10 flex items-center justify-center text-red-600 hover:bg-red-50 rounded-lg transition-colors cursor-pointer"
                      title="削除"
                    >
                      <i className="ri-delete-bin-line text-xl"></i>
                    </button>
                  </div>
                </div>
                <p className="text-gray-700 mb-4 whitespace-pre-wrap">{consultation.content}</p>
                <div className="flex items-center justify-between text-sm text-gray-500">
                  <span>
                    <i className="ri-calendar-line mr-1"></i>
                    {new Date(consultation.created_at).toLocaleString('ja-JP', {
                      year: 'numeric',
                      month: '2-digit',
                      day: '2-digit',
                      hour: '2-digit',
                      minute: '2-digit',
                      second: '2-digit',
                    })}
                  </span>
                </div>
              </div>
            ))}
          </div>
        )}
      </main>

      {showForm && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-xl shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6 border-b border-gray-100">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-bold text-gray-900">
                  {editingId ? '相談内容を編集' : '新しい相談'}
                </h2>
                <button
                  onClick={() => {
                    setShowForm(false);
                    setEditingId(null);
                    setFormData({ title: '', content: '' });
                  }}
                  className="w-10 h-10 flex items-center justify-center text-gray-400 hover:bg-gray-100 rounded-lg transition-colors cursor-pointer"
                >
                  <i className="ri-close-line text-2xl"></i>
                </button>
              </div>
            </div>

            <form onSubmit={handleSubmit} className="p-6">
              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  タイトル <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  value={formData.title}
                  onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm"
                  placeholder="相談のタイトルを入力"
                  required
                />
              </div>

              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  相談内容 <span className="text-red-500">*</span>
                </label>
                <textarea
                  value={formData.content}
                  onChange={(e) => setFormData({ ...formData, content: e.target.value })}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-transparent text-sm resize-none"
                  rows={8}
                  placeholder="相談内容を詳しく入力してください"
                  required
                />
              </div>

              <div className="flex gap-3 justify-end">
                <button
                  type="button"
                  onClick={() => {
                    setShowForm(false);
                    setEditingId(null);
                    setFormData({ title: '', content: '' });
                  }}
                  className="px-6 py-3 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-colors font-medium whitespace-nowrap cursor-pointer"
                >
                  キャンセル
                </button>
                <button
                  type="submit"
                  className="px-6 py-3 bg-pink-500 text-white rounded-lg hover:bg-pink-600 transition-colors font-medium whitespace-nowrap cursor-pointer"
                >
                  {editingId ? '更新する' : '送信する'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
